// Plotter.ts
// Responsible for handling function calls and sending them to the plotter.

import { Port } from "@/lib/Port";
import { Logger } from "@/lib/Logger";
import { GCodeParser } from "./GCodeParser";

export class Plotter {
  private static readonly endian = true; // Byte endian of plotter
  private static readonly maxErrorOneCommmand = 5; // Max CRC errors allowable for a single command

  status: GCodeStatus = GCodeStatus.STOP; // Current status
  port: Port = new Port(); // Port to send/receive commands from
  rate = 10; // Last feed rate sent
  ack = true; // Receive acknowledge
  homed = false; // Whether plotter is homed

  lastx = -1; // Last X position sent
  lasty = -1; // Last Y position sent

  // Connect to a port and set the receive callback
  public async connect(): Promise<void> {
    this.port.receiveCallback = this.receiveData.bind(this);
    await this.port.open();
    this.port.readPort();
  }

  // Send Gcode commands. Waits for a successful acknowledge before sending the next command. Retries failed commands until limit is reached.
  public async sendGCode(parser: GCodeParser): Promise<void> {
    parser.reset();
    // eslint-disable-next-line no-constant-condition
    while (true) {
      // Check pause state
      while (this.status == GCodeStatus.PAUSE) {
        await new Promise((resolve) => setTimeout(resolve, 500));
        // Be able to stop from pause state
        const status = () => {
          return this.status;
        };
        if (status() == GCodeStatus.STOP) break;
      }

      // Check stop state
      if (this.status == GCodeStatus.STOP) break;

      // Get the next command
      const command = parser.nextLine();
      if (command === null) break; // Gcode Finished

      // Skip homing if already homed
      if (command.command == PlotterCommands.HOME && this.homed) {
        continue;
      }

      this.ack = false;
      let error = 0;
      while (!this.ack) {
        // Send the command
        switch (command.command) {
          case PlotterCommands.LINEAR_TRAVERSE: {
            await this.sendXY(
              command.data[0] as number,
              command.data[1] as number,
              command.data[2] as number
            );
            break;
          }
          case PlotterCommands.STEPPER_STATE: {
            await this.sendStepperState(command.data[0] as number);
            break;
          }
          case PlotterCommands.TOOL_SELECT: {
            await this.sendToolSelect(command.data[0] as number);
            break;
          }
          case PlotterCommands.HOME: {
            await this.sendHome();
            break;
          }
          default: {
            throw new Error(`Unknown Command ${command.command}`);
          }
        }

        // Wait for acknowledge
        const ack = await this.port.ackOrTimeout();

        // Port failed to acknowledge - nothing received
        if (!ack) throw new Error("Timed out waiting for ack from plotter");

        // Acknowledged but failure returned
        if (!this.ack) {
          error++;
          if (error > Plotter.maxErrorOneCommmand) {
            throw new Error(
              "Errored out too many times trying to send one command"
            );
          }
        }
      }
    }
    Logger.info("G Code Finished");
  }

  // Sets the stepper state (On/Off)
  public async sendStepperState(state: number): Promise<void> {
    const buffer = new ArrayBuffer(2);
    const dataView = new DataView(buffer);

    dataView.setUint8(0, PlotterCommands.STEPPER_STATE);
    dataView.setUint8(1, state);

    const command = new Uint8Array(buffer);
    await this.writePlotter(command);
  }

  // Sets the selected tool (none, pen, eraser)
  public async sendToolSelect(tool: number): Promise<void> {
    const buffer = new ArrayBuffer(2);
    const dataView = new DataView(buffer);

    dataView.setUint8(0, PlotterCommands.TOOL_SELECT);
    dataView.setUint8(1, tool);

    const command = new Uint8Array(buffer);
    await this.writePlotter(command);
  }

  // Perform the homing function
  public async sendHome(): Promise<void> {
    const buffer = new ArrayBuffer(1);
    const dataView = new DataView(buffer);

    dataView.setUint8(0, PlotterCommands.HOME);

    const command = new Uint8Array(buffer);
    await this.writePlotter(command);
  }

  // Move to x,y at rate. If rate is undefined, the last rate will be used.
  public async sendXY(x: number, y: number, rate?: number): Promise<void> {
    const buffer = new ArrayBuffer(13);
    const dataView = new DataView(buffer);

    this.rate = rate ? rate : this.rate;

    dataView.setUint8(0, PlotterCommands.LINEAR_TRAVERSE);
    dataView.setFloat32(1, x, Plotter.endian);
    dataView.setFloat32(5, y, Plotter.endian);
    dataView.setFloat32(9, this.rate, Plotter.endian);

    const command = new Uint8Array(buffer);
    await this.writePlotter(command);

    this.lastx = x;
    this.lasty = y;
  }

  // Set the current position as the numbers provided
  public async sendManualHome(x: number, y: number): Promise<void> {
    const buffer = new ArrayBuffer(9);
    const dataView = new DataView(buffer);

    dataView.setUint8(0, PlotterCommands.MANUAL_HOME);
    dataView.setFloat32(1, x, Plotter.endian);
    dataView.setFloat32(5, y, Plotter.endian);

    const command = new Uint8Array(buffer);
    await this.writePlotter(command);
  }

  // Move a stepper a stepper numSteps at velocity. Velocity is proportional to the time between step pulses, so smaller numbers are quicker.
  public async sendStepper(
    stepper: PlotterSteppers,
    numSteps: number,
    velocity: number
  ): Promise<void> {
    const buffer = new ArrayBuffer(6);
    const dataView = new DataView(buffer);

    dataView.setUint8(0, PlotterCommands.MOVE_STEPPER);
    dataView.setUint8(1, stepper);
    dataView.setUint16(2, numSteps, Plotter.endian);
    dataView.setUint16(4, velocity, Plotter.endian);

    const command = new Uint8Array(buffer);
    await this.writePlotter(command);
  }

  // Writes an array to the plotter
  private async writePlotter(buffer: Uint8Array) {
    this.ack = false;
    await this.port.write(buffer);
  }

  // Callback for data received
  private async receiveData(data: Uint8Array): Promise<void> {
    const command = data[0];

    switch (command) {
      case PlotterCommands.LINEAR_TRAVERSE: {
        Logger.info("Linear Move Finished");
        this.ack = true;
        break;
      }
      case PlotterCommands.MOVE_STEPPER: {
        Logger.info("Move Stepper Finished");
        this.ack = true;
        break;
      }
      case PlotterCommands.HOME: {
        Logger.info("Finished Homing");
        this.homed = true;
        this.ack = true;
        break;
      }
      case PlotterCommands.STEPPER_STATE: {
        Logger.info("Finished Changing Stepper State");
        this.ack = true;
        break;
      }
      case PlotterCommands.TOOL_SELECT: {
        Logger.info("Changed Tool");
        this.ack = true;
        break;
      }
      case PlotterCommands.MANUAL_HOME: {
        Logger.info("Manual Home Set");
        this.homed = true;
        this.ack = true;
        break;
      }
      case PlotterCommands.CRC_ERROR: {
        Logger.warn("CRC Failed on Plotter");
        this.ack = false;
        break;
      }
      default: {
        Logger.error("Unknown response from Plotter");
        throw new Error("Unknown Response from Plotter");
      }
    }
  }
}

export interface IPlotterCommand {
  command: PlotterCommands;
  data: (string | number | null)[];
}

export class PlotterCommand {
  command: PlotterCommands;
  data: (string | number | null)[];
  constructor(command: PlotterCommands, data: (string | number | null)[] = []) {
    this.command = command;
    this.data = data;
  }
}

export enum PlotterCommands {
  NONE = 0x00,
  LINEAR_TRAVERSE = 0x01,
  MOVE_STEPPER = 0x02,
  TOOL_STATE = 0x03, // DEPRECATED
  HOME = 0x04,
  STEPPER_STATE = 0x05,
  TOOL_SELECT = 0x06,
  MANUAL_HOME = 0x07,
  CRC_ERROR = 0x08,
}

export enum PlotterToolSelect {
  NONE = 0,
  PEN = 1,
  ERASER = 2,
}

export enum PlotterStepperState {
  DISABLED = 0,
  ENABLED = 1,
}

export enum PlotterSteppers {
  LEFT = 0,
  RIGHT = 1,
}

export enum GCodeStatus {
  PLAY,
  PAUSE,
  STOP,
}
