// Creates a crc8 from of a given buffer
export function crc8(buffer: DataView): number {
  let crc = 0;
  for (let i = 0; i < buffer.byteLength; i++) {
    crc ^= buffer.getUint8(i);
    for (let k = 0; k < 8; k++) {
      crc = crc & 0x80 ? (crc << 1) ^ 0x7 : crc << 1;
      crc &= 0xff;
    }
  }
  return crc;
}
