// GCodeParser.ts
// Responsible for loading a file and parsing the Gcode into a list of plotter commands

import { Logger } from "@/lib/Logger";
import {
  PlotterCommand,
  PlotterCommands,
  PlotterStepperState,
  PlotterToolSelect,
} from "./Plotter";

export class GCodeParser {
  file: File;
  rawCommands: string[] = [];
  commands: PlotterCommand[] = [];
  counter = 0;

  // Store state between commands
  toolSelect: PlotterToolSelect = PlotterToolSelect.PEN;
  currentTool: PlotterToolSelect = PlotterToolSelect.NONE;
  stepperState: PlotterStepperState | null = null;

  constructor(file: File) {
    this.file = file;
    this.reset();
  }

  // Parse the gcode file
  async parse(): Promise<boolean> {
    const contents = await readFile(this.file);
    this.rawCommands = contents.split(/\r?\n/);

    this.reset(); // Reset state vars

    this.commands = [];
    this.addInitCommands(); // Add init commands to ensure we know state at start

    const loopArray: PlotterCommand[] = [];
    let loopIterations = 0;
    let looping = false;

    let targetArray = this.commands;

    for (const index in this.rawCommands) {
      // Parse line
      const line = this.rawCommands[parseInt(index)];

      // Ignore comments
      if (line.startsWith(";")) continue;

      // Regex match for valid Gcode commands
      const codeArray = line.match(/[GMT][0-9]+/);
      if (codeArray === null) {
        Logger.error(
          `Invalid Gcode Command on line ${parseInt(index) + 1}: ${line}`
        );
        throw new Error(
          `No Valid Gcode Commands Found on line ${
            parseInt(index) + 1
          }: ${line}`
        );
      }

      switch (codeArray[0]) {
        case "G0": {
          // Linear Traverse
          // - Sets the tool to none
          // - Moves the carriage

          this.selectTool(PlotterToolSelect.NONE, targetArray);
          const xArr = line.match(/X[0-9.]+/);
          if (xArr === null) throw new Error("No Valid X Value");
          const yArr = line.match(/Y[0-9.]+/);
          if (yArr === null) throw new Error("No Valid Y Value");
          const fVal = line.match(/F[0-9.]+/);
          const command2 = new PlotterCommand(PlotterCommands.LINEAR_TRAVERSE, [
            xArr[0].substr(1),
            yArr[0].substr(1),
            fVal === null ? null : fVal[0].substr(1),
          ]);
          targetArray.push(command2);
          break;
        }
        case "G1": {
          // Linear Draw
          // - Sets the tool to the selected tool
          // - Moves the carriage

          this.selectTool(this.toolSelect, targetArray);
          const xArr = line.match(/X[0-9.]+/);
          if (xArr === null) throw new Error("No Valid X Value");
          const yArr = line.match(/Y[0-9.]+/);
          if (yArr === null) throw new Error("No Valid Y Value");
          const fVal = line.match(/F[0-9.]+/);
          const command2 = new PlotterCommand(PlotterCommands.LINEAR_TRAVERSE, [
            xArr[0].substr(1),
            yArr[0].substr(1),
            fVal === null ? null : fVal[0].substr(1),
          ]);
          targetArray.push(command2);
          break;
        }
        case "G28": {
          // Home
          // - Homing command
          const command = new PlotterCommand(PlotterCommands.HOME);
          targetArray.push(command);
          break;
        }
        case "M17": {
          // Stepper Enable
          // - Enable steppers
          this.setStepperState(PlotterStepperState.ENABLED, targetArray);
          break;
        }
        case "M18": {
          // Stepper Disable
          // - Sets tool to none
          // - Moves tool to a safe location at the bottom center of board
          // - Disables Stepper
          this.selectTool(PlotterToolSelect.NONE);
          const command = new PlotterCommand(PlotterCommands.LINEAR_TRAVERSE, [
            300,
            10,
            null,
          ]);
          targetArray.push(command);
          this.setStepperState(PlotterStepperState.DISABLED, targetArray);
          break;
        }
        case "M808": {
          // Loop
          // First M808 needs an L<X> where <X> is the number of times to loop
          // On the next M808 the loop will be set and all commands in between will be looped X times.
          // Does not support nesting of loops

          // Check if second M808 command
          if (looping) {
            // Add all the commands
            for (; loopIterations > 0; loopIterations--) {
              this.commands = this.commands.concat(loopArray);
            }
            looping = false;
            targetArray = this.commands;
            break;
          }
          const loopArr = line.match(/L[0-9]+/);
          if (loopArr === null) throw new Error("No Valid L Value");

          looping = true;
          loopIterations = parseInt(loopArr[0].substr(1));
          targetArray = loopArray;

          break;
        }
        case "T1": {
          // Tool 1
          // - Select pen tool
          this.toolSelect = PlotterToolSelect.PEN;
          break;
        }

        case "T2": {
          // Tool 2
          // - Select eraser tool
          this.toolSelect = PlotterToolSelect.ERASER;
          break;
        }
        default: {
          // Gcode command follows correct format, but is not implemented
          Logger.error(
            `Invalid Gcode Command on line ${parseInt(index) + 1}: ${line}`
          );
          throw new Error(
            `No Valid Gcode Commands Found on line ${
              parseInt(index) + 1
            }: ${line}`
          );
        }
      }
    }

    return true;
  }

  // Reset class state
  reset(): void {
    this.counter = 0;
  }

  // Returns the next line in the command sequence. Allows the class to function as a generator for the plotter.
  nextLine(): PlotterCommand | null {
    if (this.counter >= this.commands.length) return null;
    return this.commands[this.counter++];
  }

  // Initialisation commands to set initial state
  addInitCommands(): void {
    this.selectTool(PlotterToolSelect.NONE);
    this.setStepperState(PlotterStepperState.ENABLED);
  }

  // Select the given tool
  selectTool(
    tool: PlotterToolSelect,
    arr: PlotterCommand[] = this.commands
  ): void {
    if (this.currentTool === tool) return;
    arr.push(new PlotterCommand(PlotterCommands.TOOL_SELECT, [tool]));
    this.currentTool = tool;
  }

  // Enable/Disable the steppers
  setStepperState(
    state: PlotterStepperState,
    arr: PlotterCommand[] = this.commands
  ): void {
    if (this.stepperState === state) return;
    arr.push(new PlotterCommand(PlotterCommands.STEPPER_STATE, [state]));
    this.stepperState = state;
  }
}

// Async file reader
function readFile(file: File): Promise<string> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();

    reader.onload = (res) => {
      if (res.target === null) {
        reject("Target is null");
        return;
      }
      if (typeof res.target.result !== "string")
        throw new Error("Type is not string");
      resolve(res.target.result);
    };
    reader.onerror = (err) => reject(err);

    reader.readAsText(file);
  });
}
